import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

//SERVICES
import { AbstractService } from "./AbstractService";
import { AppConfigService } from "./AppConfigService";

//MODELS
import { ResponseModelOut } from "./../models/Response";
import { GetRedeemPointsIn } from "./../models/RedeemPoints";
import { GetRedeemPointsOut } from "./../models/RedeemPoints";
import { GetMovementPointsModelIn } from "./../models/MovementPoints";
import { GetMovementPointsModelOut } from "./../models/MovementPoints";
import { GetTripsModelIn } from "./../models/TripsModel";
import { GetTripsModelOut } from "./../models/TripsModel";
import { GetMapByTripModelIn } from "./../models/TripsModel";
import { GetMapByTripModelOut } from "./../models/TripsModel";
import { SetRedeemPointsModelIn } from "./../models/TripsModel";
import { SetRedeemPointsModelOut } from "./../models/TripsModel";
import { SummaryRedeemPointsModel } from "./../models/TripsModel";
import { GetClientInfoModelOut, GetClientInfoModelIn } from "./../models/CustomerLoyaltyModel";
import { SetClientInfoModelIn, SetClientInfoModelOut } from "./../models/CustomerLoyaltyModel";
import { GetCountryModelIn, GetCountryModelOut } from "./../models/CustomerLoyaltyModel";
import { GetMunicipalityModelIn, GetMunicipalityModelOut } from "./../models/CustomerLoyaltyModel";
import { GetDepartmentModelIn, GetDepartmentModelOut } from "./../models/CustomerLoyaltyModel";
import { GetDocumentTypeClientModelIn, GetDocumentTypeClientModelOut } from "./../models/CustomerLoyaltyModel";
import { GetClientCardModelIn } from "./../models/CustomerLoyaltyModel";
import { DatabaseMessageModelOut } from "./../models/Response";
import { ResponseStateEnum } from "./../enums/ResponseStateEnum";
import { SetUploadPointsRequiredModelIn,SetUploadPointsRequiredModelOut } from "./../models/CustomerLoyaltyModel";
import { GetAllowPointRequestModelIn,GetAllowPointRequestModelOut } from "./../models/CustomerLoyaltyModel";

@Injectable()
export class CustomerLoyaltyService extends AbstractService {


    public getRedeemPointsOut: GetRedeemPointsOut;
    public tripActive: SummaryRedeemPointsModel;
    public clientInfo: GetClientInfoModelOut;

    private countries: GetCountryModelOut[];
    private municipalities: GetMunicipalityModelOut[];
    private departaments: GetDepartmentModelOut[];
    private documentTypes: GetDocumentTypeClientModelOut[];

    constructor(
        public http: Http,
        private appConfig: AppConfigService,
    ) {
        super();


        this.countries = new Array<GetCountryModelOut>();
        this.municipalities = new Array<GetMunicipalityModelOut>();
        this.departaments = new Array<GetDepartmentModelOut>();
        this.documentTypes = new Array<GetDocumentTypeClientModelOut>();
        this.clientInfo = new GetClientInfoModelOut();
    }


    public SetGetRedeemPointsOut(getRedeemPointsOutModel: GetRedeemPointsOut) {
        this.getRedeemPointsOut = getRedeemPointsOutModel;
    }

    public GetAvailablePointsRedemm(): number {

        if (this.getRedeemPointsOut !== undefined) {
            return this.getRedeemPointsOut.totalPoints/* - this.getRedeemPointsOut.redeemableMinimumPoints*/;
        }
        else {
            return 0;
        }

    }

    public SetSummaryRedeemPointsModel(model: SummaryRedeemPointsModel) {
        this.tripActive = model;
    }

    //#region "apiServices"
    public GetRedeemPoints(): Promise<ResponseModelOut<GetRedeemPointsOut>> {

        let modelIn = new GetRedeemPointsIn();
        modelIn.clientDocumentTypeNumber = 1;
        modelIn.clientDocumentNumber = this.appConfig.CONFIG.userIdentity;

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetRedeemPoints`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetTrips(modelIn: GetTripsModelIn): Promise<ResponseModelOut<Array<GetTripsModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetTrips`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetMapByTrip(modelIn: GetMapByTripModelIn): Promise<ResponseModelOut<Array<GetMapByTripModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetMapByTrip`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public SetRedeemPoints(modelIn: SetRedeemPointsModelIn): Promise<ResponseModelOut<SetRedeemPointsModelOut>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/SetRedeemPoints`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetMovementPoints(modelIn: GetMovementPointsModelIn): Promise<ResponseModelOut<Array<GetMovementPointsModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetMovementPoints`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    public GetClientInfo(modelIn: GetClientInfoModelIn): Promise<ResponseModelOut<GetClientInfoModelOut>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetClientInfo`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public SetClientInfo(modelIn: SetClientInfoModelIn): Promise<ResponseModelOut<SetClientInfoModelOut>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/SetClientInfo`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetCountry(modelIn: GetCountryModelIn): Promise<Array<GetCountryModelOut>> {

        if (this.countries.length > 0) {
            return new Promise(resolve => {
                resolve(this.countries);
            });
        }


        return new Promise(resolve => {
            this.GetCountryApi(modelIn).then(ret => {
                if (ret.statusCode = ResponseStateEnum.OK) {
                    this.countries = ret.data;
                    resolve(ret.data);
                }
                else {
                    resolve([]);
                }
            }).catch(e => { console.log(e); resolve([]); });
        });
    }
    private GetCountryApi(modelIn: GetCountryModelIn): Promise<ResponseModelOut<Array<GetCountryModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetCountry`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    public GetMunicipality(modelIn: GetMunicipalityModelIn): Promise<Array<GetMunicipalityModelOut>> {

        if (this.municipalities.length > 0) {
            return new Promise(resolve => {
                resolve(this.municipalities);
            });
        }


        return new Promise(resolve => {
            this.GetMunicipalityApi(modelIn).then(ret => {
                if (ret.statusCode = ResponseStateEnum.OK) {
                    this.municipalities = ret.data;
                    resolve(ret.data);
                }
                else {
                    resolve([]);
                }
            }).catch(e => { console.log(e); resolve([]); });
        });
    }
    private GetMunicipalityApi(modelIn: GetMunicipalityModelIn): Promise<ResponseModelOut<Array<GetMunicipalityModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetMunicipality`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetDepartment(modelIn: GetDepartmentModelIn): Promise<Array<GetDepartmentModelOut>> {

        if (this.departaments.length > 0) {
            return new Promise(resolve => {
                resolve(this.departaments);
            });
        }


        return new Promise(resolve => {
            this.GetDepartmentApi(modelIn).then(ret => {
                if (ret.statusCode = ResponseStateEnum.OK) {
                    this.departaments = ret.data;
                    resolve(ret.data);
                }
                else {
                    resolve([]);
                }
            }).catch(e => { console.log(e); resolve([]); });
        });
    }
    private GetDepartmentApi(modelIn: GetDepartmentModelIn): Promise<ResponseModelOut<Array<GetDepartmentModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetDepartment`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetDocumentTypeClient(modelIn: GetDocumentTypeClientModelIn): Promise<Array<GetDocumentTypeClientModelOut>> {

        if (this.documentTypes.length > 0) {
            return new Promise(resolve => {
                resolve(this.documentTypes);
            });
        }


        return new Promise(resolve => {
            this.GetDocumentTypeClientApi(modelIn).then(ret => {
                if (ret.statusCode = ResponseStateEnum.OK) {
                    this.documentTypes = ret.data;
                    resolve(ret.data);
                }
                else {
                    resolve([]);
                }
            }).catch(e => { console.log(e); resolve([]); });
        });
    }
    private GetDocumentTypeClientApi(modelIn: GetDocumentTypeClientModelIn): Promise<ResponseModelOut<Array<GetDocumentTypeClientModelOut>>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetDocumentTypeClient`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    public SendMailCustomerCard(modelIn: GetClientCardModelIn): Promise<ResponseModelOut<DatabaseMessageModelOut>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/SendMailCustomerCard`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public SetUploadPointsRequired(modelIn: any): Promise<ResponseModelOut<SetUploadPointsRequiredModelOut>> {

        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/SetUploadPointsRequired`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public GetAllowPointRequestApi(): Promise<ResponseModelOut<GetAllowPointRequestModelOut>> {

        let modelIn = new GetAllowPointRequestModelIn();
        modelIn.documentClientNumber=this.clientInfo.documentClientNumber;
        modelIn.documentTypeClientId=this.clientInfo.documentTypeClientId;


        return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetAllowPointRequest`, modelIn, this.GetHeader())
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    //#endregion  "apiServices" 
}


