import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

//SERVICES
import { AbstractService } from "./AbstractService";
import { AppConfigService } from "./AppConfigService";

//MODELS
import { ResponseModelOut } from "./../models/Response";
import {  GetAgenciesModelOut } from "./../models/AgencyModel";
//import { Promise } from 'protractor/node_modules/@types/q';
import { ResponseStateEnum } from "./../enums/ResponseStateEnum";

@Injectable()
export class AgencyService extends AbstractService {


    agencies:Array<GetAgenciesModelOut>;

    constructor(
        private http: Http,
        private appConfig:AppConfigService
    ) {
        super();
        this.agencies= new Array<GetAgenciesModelOut>();
    }

    public GetAgencies(): Promise<Array<GetAgenciesModelOut>> {
        
            if(this.agencies.length>0)
            {
                return new Promise<Array<GetAgenciesModelOut>>(resolve=>{
                    resolve(this.agencies);
                });
            }

            return new Promise<Array<GetAgenciesModelOut>>(resolve=>{

                this.GetAgenciesApi().then(ret=>{
                    if (ret.statusCode == ResponseStateEnum.OK) {
                        this.agencies=ret.data;
                        resolve(this.agencies);
                    }
                    else
                    {
                        resolve([]);
                    }
                }).catch(e=>{console.log(e);resolve([]);});

                

            });
    }

    //#region "apiServices"
    private GetAgenciesApi(): Promise<ResponseModelOut<Array<GetAgenciesModelOut>>> {

                return this.http.post(`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/V1/CustomerLoyalty/GetAgencies`,{},this.GetHeader())
                    .toPromise()
                    .then(this.extractData)
                    .catch(this.handleError);
    }
    //#endregion  "apiServices"
        
}   


