import { Component, OnInit,Input,Output,EventEmitter  } from '@angular/core';

//MODELS
import { SeatModel } from "../../models/TripsModel";


@Component({
    selector: 'seat-component',
    templateUrl: './seat.component.html',
    styleUrls: ['./seat.component.scss']

})

export class SeatComponent implements OnInit {

    @Input()
    seatData:SeatModel;

    @Input()
    SeatActive:SeatModel;

    @Input()
    PATH:string;
    
    private image:string;
    private className:string;


    IsSpace=false;

    @Output() seatSelectedUpdated = new EventEmitter();

    constructor() {
        
     }

    ngOnInit() { 
        
        //console.log(this.seatData);

        let item="";
        this.className="icon-bus";
        switch (this.seatData.itemTypeCode) {
            case 'P'://pasillo
                item='Floor';
                this.className='floor-bus';
                this.IsSpace=true;
                break;
            case 'S'://silla
                item='Seat';
                this.className='seat-bus';
                break;
            case 'V'://vacio
                item='Floor';
                this.IsSpace=true;
                break;
            case 'B'://bao
                item='restroom-sign';
                break;
            case 'U'://puerta
                item='door';
                break;
            case 'E'://escalera
                item='stairs';
                break;
            case 'C'://conductor
                item='driver';
                break;
            default:
                break;
        }

        let typeItem="";

        switch (this.seatData.seatStatusApproved) {
            case 'N':
                typeItem="";
                break;
            case 'L':
                typeItem="Green";
                break;
            case 'V':
                typeItem="Red";
                break;
            case 'B':
                typeItem="Red";
                break;
            case 'E':
                typeItem="Red";
            case 'A':
                typeItem="Red";
            case 'W':
                typeItem="Red";
            case 'R':
                typeItem="Red";
                break;
            case 'I':
                typeItem="Red";
                break;
            default:
                typeItem="Red";
                break;
        }

   


        this.image=`${this.PATH}/assets/img/icons/${item}${typeItem}.png`;
    }

    SelectedSeat()
    {
        if(this.className=='seat-bus' && this.seatData.seatStatusApproved=='L')
        {
            this.seatSelectedUpdated.emit(this.seatData);
        }
        
    }
}


