import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

//SERVICES
import { AbstractService } from "./AbstractService";
import { AppConfigService } from "./AppConfigService";


//MODELS
import { ResponseStateEnum } from "./../enums/ResponseStateEnum";
import { ResponseModelOut } from "./../models/Response";
import { LoginModelIn } from "./../models/AuthModel";
import { LoginModelOut } from "./../models/AuthModel";

@Injectable()
export class AuthenticationService extends AbstractService {


    IsRefreshToken:boolean;
    constructor(
        public http: Http,
        private appConfig:AppConfigService,
    ) {
        super();
    }


    public Valid()
    {
        
    
    }


    public refreshToken()
    {
        let expireDate=this.GetTokenExpire();

        
        let today=new Date();
        var dif = today.getTime()-expireDate.getTime();
        dif=dif*(-1);

        if(dif<300000 && !this.IsRefreshToken)//5 minutos antes de vencer se refresca token
        {
            this.IsRefreshToken=true;
            this.RefreshToken().then(ret=>{
               
                if (ret.statusCode = ResponseStateEnum.OK) {
                    this.SetToken(ret.data.token,ret.data.expires);
                }

                this.IsRefreshToken=false;
            }).catch(e=>{

                location.reload();//si hay un error al pedir token refrescar web page
            })
        }

        var self = this;
        
        let timeExecute = dif-240000;// reviso 4 minutos antes de vencer

        if(timeExecute<0)
        {
            timeExecute = 3000;// rivisar en 0.5 minutos si ya llego el renewtoken
        }
    
        setTimeout(()=>{
            self.refreshToken();
        }, timeExecute);

        


     
    }

    //#region "apiServices"
    public LoginUser(): Promise<ResponseModelOut<LoginModelOut>> { 

        
        let modelIn:LoginModelIn={UserName:this.appConfig.CONFIG.WebApiUserName,Password:this.appConfig.CONFIG.WebApiPasword};
        return this.http.post(`${this.appConfig.CONFIG.AppWebApiAuthUri}/V1/Authentication/UserLogin`,modelIn,this.GetHeader(false))
            .toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    public RefreshToken(): Promise<ResponseModelOut<LoginModelOut>> { 
        
                
                let modelIn:LoginModelIn={UserName:this.appConfig.CONFIG.WebApiUserName,Password:''};
                return this.http.post(`${this.appConfig.CONFIG.AppWebApiAuthUri}/V1/Authentication/UserTokenRenew`,modelIn,this.GetHeader())
                    .toPromise()
                    .then(this.extractData)
                    .catch(this.handleError);
            }
    //#endregion  "apiServices" 
}


