import { Component, OnInit } from '@angular/core';
import { FormsModule,FormControl,FormGroup,FormBuilder,Validators } from '@angular/forms';
import { Router } from "@angular/router";
import {SuiLocalizationService} from "ng2-semantic-ui";
import es from "ng2-semantic-ui/locales/es";


//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { AgencyService } from "../../services/AgencyService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

//MODELS
import { ResponseStateEnum } from "../../enums/ResponseStateEnum";
import { GetAgenciesModelOut } from "../../models/AgencyModel";
import { GetTripsModelIn,GetTripsModelOut,GetMapByTripModelIn,GetMapByTripModelOut,SummaryRedeemPointsModel,SeatModel } from "../../models/TripsModel";

//ANIMATIONS
import { PageRedemptionAnimation } from "./pageRedemption.animation";
@Component({
    selector: 'pageRedemption-component',
    templateUrl: './pageRedemption.component.html',
    styleUrls: ['./pageRedemption.component.scss'],
    animations:PageRedemptionAnimation
})

export class PageRedemptionComponent implements OnInit {

    public _PATH:string;
    public tripsModelList: Array<GetTripsModelOut>;
    public agenciesModelList: Array<GetAgenciesModelOut>;
    public isTripEnabled: boolean;
    public getTripModelIn: GetTripsModelIn;
    public getTripModelOut: GetTripsModelOut;
    public getMapByTripModelOut:GetMapByTripModelOut[];
    public seatSelected:SeatModel;

    public ThereAreTrips:boolean;
    public searchTripFilter:string;

    public IsVisibleTripSelected:boolean;
    public IsVisibleSeatsSelected:boolean;

    tripQueryForm: FormGroup;
    showMessageForm:boolean;
    aviablePoints:number;


    CanConfirmTrip:boolean;
    
    public minDate:Date;
    constructor(
        public agencyService: AgencyService,
        public customerLoyaltyService:CustomerLoyaltyService,
        private fb: FormBuilder,
        public router:Router,
        private appConfig:AppConfigService,
        public localizationService:SuiLocalizationService
    ) {

        localizationService.load("es", es);
        localizationService.setLanguage("es");
        let today = new Date();
        this.minDate = new Date(today.getFullYear(), today.getMonth(), today.getDate());
      //  this.minDate=new Date(2017,09,20); 
      //  this.minDate='2017-09-20';//new Date(2017,09,20);      
        
        this.getTripModelIn = new GetTripsModelIn();
        this.getTripModelOut = new GetTripsModelOut();


        this.showMessageForm=false; 
        this.tripQueryForm= this.fb.group({
            originAgency: [null, Validators.required ],
            destinationAgency:[null, Validators.required ],
            tripDate:[null,Validators.required ] // <--- the FormControl called "name"
          });


          this.tripQueryForm.valueChanges.subscribe((o) => {
                //lista
               this.IsVisibleTripSelected=false;
               this.IsVisibleSeatsSelected=false;
               this.tripsModelList=new Array<GetTripsModelOut>();

            });
    }

    ngOnInit() {

        if(this.appConfig.CONFIG===undefined)
        {
            this.appConfig.ActiveUrl="/redencion";
            this.router.navigate(['/']);
        }
        else{
            
            this._PATH=this.appConfig.CONFIG.path;


            this.agencyService.GetAgencies().then((ret) => {
                this.agenciesModelList = ret;
            });
    
            this.evaluateTripEnabled();
    
            this.aviablePoints=this.customerLoyaltyService.GetAvailablePointsRedemm();
    
            this.CanConfirmTrip=false;
        }


        

      
    }

    evaluateTripEnabled() {
        this.isTripEnabled = true;
    }

    FillTrips() {

        if(this.tripQueryForm.status=='VALID')
        {
            this.getTripModelIn.agencySaleId = this.tripQueryForm.value.originAgency.agencyId;
            this.getTripModelIn.agencySaleName = this.tripQueryForm.value.originAgency.agencyName;
            this.getTripModelIn.agencyDestinationId = this.tripQueryForm.value.destinationAgency.agencyId;
            this.getTripModelIn.agencyDestinationName = this.tripQueryForm.value.destinationAgency.agencyName;
            this.getTripModelIn.tripDate=this.tripQueryForm.value.tripDate;
            this.getTripModelIn.tripTimeStart = "00:00";
            this.getTripModelIn.tripTimeEnd = "23:59";
    
            this.customerLoyaltyService.GetTrips(this.getTripModelIn).then((ret) => {

                if (ret.statusCode == ResponseStateEnum.OK) {
                    

                    this.tripsModelList=ret.data;
                    
                    this.ThereAreTrips=ret.recordCount==0;
    
                   
    
                    this.IsVisibleTripSelected=true;


                    this.CanConfirmTrip=false;
                    this.IsVisibleSeatsSelected=false;
                }
            });
        }
        else{
            this.showMessageForm=true;
        }

       
    
    }

    ResolveUrlIcon(url:string)
    {
       if(url===undefined || url==null)
        {
            return "";
        }

        return url.replace('{back_resource}',this.appConfig.CONFIG.AppWebApiResourcesUri);
    }

    SelectTrip(tripModel:GetTripsModelOut)
    {
       
        this.getTripModelOut=tripModel;


        let modelIn = new GetMapByTripModelIn();
        modelIn.companyId=tripModel.companyId;
        modelIn.mapOrientation="V";
        modelIn.tripAgencyId=tripModel.tripAgencyId;
        modelIn.mapTypeId=1;
        modelIn.agencyOriginId=tripModel.agencyOriginId;
        modelIn.agencyDestinationId=tripModel.agencyDestinationId;

        this.customerLoyaltyService.GetMapByTrip(modelIn).then((ret)=>{
            if (ret.statusCode == ResponseStateEnum.OK) {
                this.getMapByTripModelOut=ret.data;
                this.IsVisibleSeatsSelected=true;
                this.CanConfirmTrip=false;
            }

        }).catch((e)=>console.log(e));

    }

    SeatSelected(seat:SeatModel)
    {
        this.seatSelected=seat;
        this.CanConfirmTrip=true;
    }


    ConfirmTrip()
    {
        var model= new SummaryRedeemPointsModel();
        
        model.tripId=this.getTripModelOut.tripId;
        model.agencyDestinationId=this.getTripModelIn.agencyDestinationId;
        model.agencyOriginId=this.getTripModelIn.agencySaleId;
        model.agencyOriginName=this.getTripModelIn.agencySaleName;
        model.agencyDestinationName=this.getTripModelIn.agencyDestinationName;
        model.seatName=this.seatSelected.itemCodeApproved;
        model.seatId=this.seatSelected.itemCodeBase;
        model.companyShortName=this.getTripModelOut.companyShortName;
        model.serviceTypeLogoUrl=this.getTripModelOut.serviceTypeLogoUrl;
        model.serviceTypeName=this.getTripModelOut.serviceTypeName;
        model.tripDate=this.getTripModelOut.departureDate;
        model.tripKey=this.getTripModelOut.tripKey;
        model.clientName=this.customerLoyaltyService.getRedeemPointsOut.clientName;
        model.valueToRedeem=this.getTripModelOut.ratePerSeat;
        model.tripTime=this.getTripModelOut.tripTime;


        this.customerLoyaltyService.SetSummaryRedeemPointsModel(model);

        this.router.navigate(['/resumen-viaje']);

    }



}


