import { Injectable,OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

//SERVICES
import { AbstractService } from "./AbstractService";

//MODELS
import { ResponseModelOut } from "./../models/Response";
import {  GetAgenciesModelOut } from "./../models/AgencyModel";




@Injectable()
export class AppConfigService extends AbstractService{


    CONFIG:any;
    public ActiveUrl:string;
    private path:string;
    constructor( 
        private http:Http
    ) {
        super();
        this.ActiveUrl="";

        this.path=(window as any).resourcesPath;
    }


    public LoadParams(config:any)
    {
        this.CONFIG=config;
        this.CONFIG.path=(window as any).resourcesPath;
        this.CONFIG.userIdentity=(window as any).identification;
    }

    //#region "apiServices"
    public GetAppConfig(): Promise<any> {
        
        return this.http.get(`${this.path}/appconfig/config.${this._env.Environment}.json`)
                .toPromise()
                .then(this.extractData)
                .catch(this.handleError);
    }
    //#endregion  "apiServices"

}   


