import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


//MODELS
import { ResponseStateEnum } from "../../enums/ResponseStateEnum";
import { GetRedeemPointsIn } from "../../models/RedeemPoints";
import { GetClientInfoModelIn, GetClientInfoModelOut } from "../../models/CustomerLoyaltyModel";

//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { AuthenticationService } from "../../services/AuthenticationService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";


@Component({
    selector: 'pageLoading-component',
    templateUrl: './PageLoading.component.html',
    styleUrls: ['./PageLoading.component.scss']

})

export class PageLoadingComponent implements OnInit {

    segmentDimmed: boolean;
    message: string;
    constructor(
        private appConfig: AppConfigService,
        public authenticationService: AuthenticationService,
        public customerLoyaltyService: CustomerLoyaltyService,
        private router: Router
    ) { }

    ngOnInit() {
        this.segmentDimmed = true;
        this.message = "Conectando al servidor";

        this.appConfig.GetAppConfig().then((ret) => {
            this.appConfig.LoadParams(ret);
            this.LoadData();
        });
    }


    LoadData() {

        this.authenticationService.LoginUser().then((ret) => {

            this.message = "Validando sesin";


            if (ret.statusCode = ResponseStateEnum.OK) {
                this.authenticationService.SetToken(ret.data.token,ret.data.expires);
                this.authenticationService.refreshToken();
                this.message = "Validando usuario";

                if (this.appConfig.CONFIG.userIdentity === undefined || this.appConfig.CONFIG.userIdentity == null || this.appConfig.CONFIG.userIdentity == '') {

                    this.message = "Usuario no autenticado, por favor ingrese al Login.";
                    this.segmentDimmed = false;
                    return;
                }

                //update RedeemPoints
                this.customerLoyaltyService.GetRedeemPoints().then((ret) => {
                
                    if (ret.statusCode == ResponseStateEnum.OK && ret.data!=null) {

                        if (ret.data.userIsTraveler == 'S') {

                            this.customerLoyaltyService.SetGetRedeemPointsOut(ret.data);
                            this.message = "Obteniendo informacin del cliente";


                            //update ClientInfo
                            let modelIn = new GetClientInfoModelIn();
                            modelIn.documentClientNumber = this.appConfig.CONFIG.userIdentity;
                            modelIn.documentTypeClientId = 1;

                            this.customerLoyaltyService.GetClientInfo(modelIn).then((ret) => {
                                if (ret.statusCode = ResponseStateEnum.OK) {
                                    this.message = "Iniciando aplicacin";

                                    this.customerLoyaltyService.clientInfo = ret.data;

                                    if (this.appConfig.ActiveUrl !== undefined && this.appConfig.ActiveUrl.indexOf('/') != -1) {
                                        let url = this.appConfig.ActiveUrl;
                                        this.appConfig.ActiveUrl = undefined;
                                        this.router.navigate([url]);
                                    }
                                    else {
                                        this.router.navigate(['/resumen-puntos']);
                                    }
   
                                }
                            });

                           

                        }
                        else {
                            this.message = "Actualmente usted no se encuentra inscrito en el plan viajero expreso.";
                            this.segmentDimmed = false;
                        }


                    }
                    else {
                        this.message = "Error de comunicacin con el servidor.";
                        this.segmentDimmed = false;
                    }
                });

            }
            else {
                alert(ret.message);
            }
        }).catch((e) => {
            this.message = "No se puede establecer conexin con el servidor.";
            this.segmentDimmed = false;
        });
    }
}       


