import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';


//MODELS
import { GetMapByTripModelOut,SeatModel } from "../../models/TripsModel";


@Component({
    selector: 'bus-component',
    templateUrl: './bus.component.html',
    styleUrls: ['./bus.component.scss'],
})

export class BusComponent implements OnInit {

    //data:Array<GetMapByTripModelOut>;

    @Input()
    data:Array<GetMapByTripModelOut>;
    
    @Input()
    orientation:string;

    @Input()
    maxPoints:number;

    @Input()
    hasTwoFloors:boolean;

    @Output()
    seatSelectedUpdated = new EventEmitter();

    
    pageDimmed:boolean;

    seatSelected:SeatModel;

    floorActiveIndex:Number;

    seatRate:number;

    @Input()
    PATH:string;
    constructor(

    ) {

     }

    ngOnInit() { 
        this.hasTwoFloors=this.data.length>1;
        this.floorActiveIndex=1;

    }


    SelectedBus(seat:SeatModel)
    {
        this.seatRate=seat.seatRate;
        if(this.maxPoints<seat.seatRate)
        {
            this.pageDimmed=true;
        }
        else{
            this.seatSelected=seat;
            this.seatSelectedUpdated.emit(seat);
        }

       
    }

    compare(i:number,row:any[]):boolean
    {
        let typeItem=row[i].itemTypeCode;
        if(typeItem=='S' || typeItem=='P' || typeItem=='V')
        {
            return true;
        }

        if(i>0 && i<=2)
        {
            if(row[i-1].itemTypeCode==row[i].itemTypeCode)
            {
                return false;
            }
        }


        if(i>2 && i<5)
        {
            if(row[i+1].itemTypeCode==row[i].itemTypeCode)
            {
                return false;
            }
        }

        return true;
    }
  
}


