import { Component, OnInit,Input } from '@angular/core';
import {  Router }    from '@angular/router';

//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

//MODELS
import { ResponseStateEnum } from "../../enums/ResponseStateEnum";
import { UserModel } from "../../models/UserModel";
import { PuntuacionModel } from "../../models/Puntuacion";
import { GetRedeemPointsOut } from "../../models/RedeemPoints";
import { GetClientCardModelIn } from "../../models/CustomerLoyaltyModel";

//ANIMATION
import {ResumePointAnimation } from "./resumePoints.animation";

@Component({
    selector: 'resumePoints-component',
    templateUrl: './resumePoints.component.html',
    styleUrls: ['./resumePoints.component.scss'],
    animations:ResumePointAnimation

})

export class ResumePointsComponent implements OnInit {


    @Input()
    fullWidth:boolean;

    @Input()
    isResizable:boolean;


    _PATH:string;
    getRedeemPointsOut:GetRedeemPointsOut;

    pathCardImage:string;

    CanSendMail:boolean;
    IsMailSender:boolean;
    MailCustomer:string;
    constructor(
        private customerLoyaltyService:CustomerLoyaltyService,
        private router: Router,
        private appConfig:AppConfigService
    ) { 


        this.fullWidth=true;
        this.isResizable=false;
        this.getRedeemPointsOut= new GetRedeemPointsOut();
        this.CanSendMail=false;
       
    }

    ngOnInit() { 
        if(this.appConfig.CONFIG===undefined)
        {
            this.appConfig.ActiveUrl=this.router.url;
            this.router.navigate(['/']);
        }   
        else{
            this.getRedeemPointsOut=this.customerLoyaltyService.getRedeemPointsOut;
            this._PATH=this.appConfig.CONFIG.path;

            this.pathCardImage=`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/CardImage/?code=${this.customerLoyaltyService.getRedeemPointsOut.imageHash}`;

            this.MailCustomer=this.customerLoyaltyService.clientInfo.clientMail.toLocaleLowerCase();
        }
    }

    SendMailCardEvent()
    {
        this.IsMailSender=false;
        this.CanSendMail=false;
        var modelIn = new GetClientCardModelIn();

        modelIn.documentClientNumber=this.customerLoyaltyService.clientInfo.documentClientNumber;
        modelIn.documentTypeClientId=this.customerLoyaltyService.clientInfo.documentTypeClientId;

        this.customerLoyaltyService.SendMailCustomerCard(modelIn).then((ret)=>{
            if (ret.statusCode == ResponseStateEnum.OK) {
                this.IsMailSender=true;
            } 
        }).catch(e=>console.log(e));
    }
}   


