import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";

//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { AuthenticationService } from "../../services/AuthenticationService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

@Component({
    selector: 'pageHome-component',
    templateUrl: './pageHome.component.html',
    styleUrls: ['./pageHome.component.scss']

})

export class PageHomeComponent implements OnInit {

    _PATH:string;
    pathCardImage:string;
    constructor(
        public authenticationService:AuthenticationService,
        private appConfig:AppConfigService,
        private router:Router,
        public customerLoyaltyService: CustomerLoyaltyService,
    ) { }

    ngOnInit() { 
        
        if(this.appConfig.CONFIG===undefined)
        {

            this.router.navigate(['/']);
        }
        else{
            this._PATH=this.appConfig.CONFIG.path;

            this.pathCardImage=`${this.appConfig.CONFIG.AppWebApiCustomerLoyaltyUri}/CardImage/?code=${this.customerLoyaltyService.getRedeemPointsOut.imageHash}`;
        }

    }
}


