import { Component, OnInit,Input } from '@angular/core';

//SERVICES

//MODELS
import { ItemMenuModel } from "../../models/ItemMenuModel";

@Component({
    selector: 'menu-component',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.scss']

})

export class MenuComponent implements OnInit {

    menuItems:ItemMenuModel[];
    @Input()
    itemActive:number;
    constructor() { 
        this.itemActive=0;
        this.menuItems= new Array<ItemMenuModel>();    
        
        this.menuItems[0]={name:'Inicio',link:'/resumen-puntos'};
        /*this.menuItems[1]={name:'Solicitud de puntos',link:'/solicitud-puntos'};*/
        this.menuItems[1]={name:'Redencin de puntos',link:'/redencion'};
        this.menuItems[2]={name:'Actualizacin de datos',link:'/actualizacion-datos'};
        this.menuItems[3]={name:'Movimiento de puntos',link:'/movimiento-puntos'};
    }

    ngOnInit() { }
}


