import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'ordinalNumber' })
export class OrdinalNumberingPipe implements PipeTransform {
    transform(num: number, cadena: string): string {


        switch (num) {
            case 1:
                cadena = cadena.replace("_num_", "Primer");
                break;
            case 2:
                cadena = cadena.replace("_num_", "Segundo");
                break;
            case 3:
                cadena = cadena.replace("_num_", "Tercer");
                break;
            case 4:
                cadena = cadena.replace("_num_", "Cuarto");
                break;
            case 5:
                cadena = cadena.replace("_num_", "Quinto");
                break;
            case 6:
                cadena = cadena.replace("_num_", "Sexto");
                break;
            case 7:
                cadena = cadena.replace("_num_", "Sptimo");
                break;
            case 8:
                cadena = cadena.replace("_num_", "Octavo");
                break;
            case 9:
                cadena = cadena.replace("_num_", "Noveno");
                break;
            case 10:
                cadena = cadena.replace("_num_", "Dcimo");
                break;

            default:
                break;
        }

        return cadena;
    }
}


