import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

//MODELS
//import { SummaryRedeemPointsModel,SetRedeemPointsModelIn } from "../../models/TripsModel";


//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

//ANIMATION
import { PageResultTripAnimation } from "./pageResultTrip.animation";

@Component({
    selector: 'pageResultTrip-component',
    templateUrl: './pageResultTrip.component.html',
    styleUrls: ['./pageResultTrip.component.scss'],
    animations:PageResultTripAnimation
    
})

export class PageResultTripComponent implements OnInit {

    PATH:string;
    PurchaseId:number;
    constructor(
       private customerLoyaltyService:CustomerLoyaltyService,
       private appConfig:AppConfigService,
       private router:Router
    ) { 

    }

    ngOnInit() { 

        if(this.appConfig.CONFIG===undefined)
        {
            this.router.navigate(['/']);
        }
        else{
            this.PATH=this.appConfig.CONFIG.path;
            this.PurchaseId=this.customerLoyaltyService.tripActive.purchaseId;
        }

      
    }


  
}


