import { Component, OnInit, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { FormsModule, FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';

//MODELS
import { ResponseStateEnum } from "../../enums/ResponseStateEnum";
import { GetMovementPointsModelIn, GetMovementPointsModelOut } from "../../models/MovementPoints";


//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

//ANIMATION
import { PagePointMovementAnimation } from "./pagePointMovement.animation";

@Component({
    selector: 'pagePointMovement-component',
    templateUrl: './pagePointMovement.component.html',
    styleUrls: ['./pagePointMovement.component.scss'],
    animations: PagePointMovementAnimation

})

export class PagePointMovementComponent implements OnInit {

    PATH: string;
    year: number;
    month: string;
    searchConceptFilter:string="";

    months: Array<string>;
    concepts: Array<string>;
    movements: Array<GetMovementPointsModelOut>;
    countRedemmPoints: number;
    countTripPoints: number;
    discountPointsMonth: number;
    addPointsMonth: number;
    movementPointsModelIn:GetMovementPointsModelIn;

    queryForm: FormGroup;

    constructor(
        private customerLoyaltyService: CustomerLoyaltyService,
        private appConfig: AppConfigService,
        private router: Router,
        private fb: FormBuilder,
    ) {

        this.months = new Array<string>();
        this.months.push('Enero');
        this.months.push('Febrero');
        this.months.push('Marzo');
        this.months.push('Abril');
        this.months.push('Mayo');
        this.months.push('Junio');
        this.months.push('Julio');
        this.months.push('Agosto');
        this.months.push('Septiembre');
        this.months.push('Octubre');
        this.months.push('Noviembre');
        this.months.push('Diciembre');

        var today = new Date();

        this.queryForm = this.fb.group({
            month: new FormControl(this.months[today.getMonth()], [Validators.required]),
            year: new FormControl(today.getFullYear(), [Validators.required, Validators.pattern('[0-9]*'), Validators.maxLength(4), Validators.minLength(4)]),
            concept:new FormControl(''),
        });

        this.movementPointsModelIn= new GetMovementPointsModelIn();
        this.queryForm.valueChanges.subscribe(() => {           


            if (!this.queryForm.invalid) {

                //validar que solo consulte ao 2017 - Dic
                let yearSelected:number = this.queryForm.get('year').value;

                if(yearSelected<=2016 || yearSelected>(new Date()).getFullYear())
                {
                    return;
                }

               
               let monthCalculated=this.months.indexOf(this.queryForm.get('month').value);
               
                let monthNumber=(monthCalculated==11)?12:(monthCalculated+ 1);
           
                if(yearSelected==2017 && monthNumber!=12)
                {
                    return;
                }




                this.GetMovementPoints();
            }
        });


    }

    ngOnInit() {

        if (this.appConfig.CONFIG === undefined) {
            this.appConfig.ActiveUrl="/movimiento-puntos";
            this.router.navigate(['/']);
        }
        else {
            this.PATH = this.appConfig.CONFIG.path;


            this.GetMovementPoints();
        }


    }


    ResolvePoints(model: GetMovementPointsModelOut): GetMovementPointsModelOut {

        if (model.nameTransactionPointsConcept.toLowerCase().indexOf('red') !== -1) {
            this.countRedemmPoints++;
        }

        if (model.nameTransactionPointsConcept.toLowerCase().indexOf('acumul') !== -1) {
            this.countTripPoints++;
        }

        if(this.concepts.indexOf(model.nameTransactionPointsConcept)==-1)
        {
            this.concepts.push(model.nameTransactionPointsConcept);
        }

        if (model.initialPoints != 0) {
            model.pointValue = model.initialPoints;
            model.signOperation = '';
           // this.discountPointsMonth = model.pointValue;
            return model;
        }

        if (model.entryPoints != 0) {
            model.pointValue = model.entryPoints;
            model.signOperation = '+';
            this.addPointsMonth += model.pointValue;
            return model;
        }

        if (model.pointSpending != 0) {
            model.pointValue = model.pointSpending;
            model.signOperation = '-';
            this.discountPointsMonth += model.pointValue;
            return model;
        }



        return model;
    }

    GetMovementPoints() {
        this.movements = new Array<GetMovementPointsModelOut>();
        this.concepts= new Array<string>();
        this.countRedemmPoints = 0;
        this.countTripPoints = 0;
        this.discountPointsMonth = 0;
        this.addPointsMonth=0;

        this.movementPointsModelIn = new GetMovementPointsModelIn();
        this.movementPointsModelIn.clientDocumentNumber = this.customerLoyaltyService.getRedeemPointsOut.clientDocumentNumber;
        this.movementPointsModelIn.clientDocumentTypeNumber = this.customerLoyaltyService.getRedeemPointsOut.clientDocumentTypeNumber;
        let monthCalculated=this.months.indexOf(this.queryForm.get('month').value);//result month -1
        this.movementPointsModelIn.monthString=this.queryForm.get('month').value;
        this.movementPointsModelIn.queryMonth = (monthCalculated==11)?12:(monthCalculated+ 1);
        this.movementPointsModelIn.queryYear = this.queryForm.get('year').value;

        this.customerLoyaltyService.GetMovementPoints( this.movementPointsModelIn).then((ret) => {

            if (ret.statusCode = ResponseStateEnum.OK) {
                //console.log(ret.data);
                for (let entry of ret.data) {
                    this.movements.push(this.ResolvePoints(entry));
                }
                this.movements = ret.data;
            }
        }).catch((e) => console.log(e));
    }



}


