import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormsModule, FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import {SuiLocalizationService} from "ng2-semantic-ui";
import es from "ng2-semantic-ui/locales/es";
import { Observable } from 'rxjs/Observable';


//MODELS
import { GetClientInfoModelOut, GetClientInfoModelIn } from "../../models/CustomerLoyaltyModel";
import { SetClientInfoModelIn, SetClientInfoModelOut } from "../../models/CustomerLoyaltyModel";
import { GetCountryModelOut, GetCountryModelIn, GetMunicipalityModelOut, GetMunicipalityModelIn, GetDepartmentModelOut, GetDepartmentModelIn,GetDocumentTypeClientModelIn,GetDocumentTypeClientModelOut} from "../../models/CustomerLoyaltyModel";
import { ResponseStateEnum } from "../../enums/ResponseStateEnum";

//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

//ANIMATION
import { PageUpdateUserDataAnimation } from "./pageUpdateUserData.animation";

@Component({
    selector: 'pageUpdateUserData-component',
    templateUrl: './pageUpdateUserData.component.html',
    styleUrls: ['./pageUpdateUserData.component.scss'],
    animations: PageUpdateUserDataAnimation

})

export class PageUpdateUserDataComponent implements OnInit {

    PATH: string;
    PurchaseId: number;
    queryForm: FormGroup;
    documentTypesClient: GetDocumentTypeClientModelOut[];
    clientGenders: any[];


    countries: Array<GetCountryModelOut>;
    departments: GetDepartmentModelOut[];
    municipalities: GetMunicipalityModelOut[];



    IsEditing: boolean = false;
    IsUpdated:boolean=false;

    constructor(
        private customerLoyaltyService: CustomerLoyaltyService,
        private appConfig: AppConfigService,
        private router: Router,
        private fb: FormBuilder,
        public localizationService:SuiLocalizationService
    ) {

        localizationService.load("es", es);
        localizationService.setLanguage("es");


        this.clientGenders = [
            { id: 'N', name: 'No especifica' },
            { id: 'H', name: 'Hombre' },
            { id: 'M', name: 'Mujer' }
        ]

        this.countries = new Array<GetCountryModelOut>();


        this.queryForm = this.fb.group({
            documentTypeClient: new FormControl({ value: null, disabled: (!this.IsEditing) }, [Validators.required]),
            documentClientNumber: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required]),
            clientNames: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required,Validators.maxLength(100)]),
            clientLastNames: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required,Validators.maxLength(100)]),
            clientCountry: new FormControl({ value: null, disabled: (!this.IsEditing) }, [Validators.required]),
            clientDepartment: new FormControl({ value: null, disabled: (!this.IsEditing) }, [Validators.required]),
            clientCity: new FormControl({ value: null, disabled: (!this.IsEditing) }, [Validators.required]),
            clientAddress: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required,Validators.maxLength(100)]),
            clientTelephone1: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required,Validators.maxLength(100),Validators.pattern('[0-9]*'),Validators.maxLength(10)]),
            clientTelephone2: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required,Validators.maxLength(100),Validators.pattern('[0-9]*')]),
            clientMail: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required,Validators.maxLength(100),Validators.email]),
            clientBirthDate: new FormControl({ value: '', disabled: (!this.IsEditing) }, [Validators.required]),
            clientGender: new FormControl({ value: null, disabled: (!this.IsEditing) }, [Validators.required]),

        });

        //Load Departments
        this.queryForm.controls.clientCountry.valueChanges.subscribe(() => {
            let country = this.queryForm.controls.clientCountry.value;
            let modelIn = new GetDepartmentModelIn();
            modelIn.CountryId = country.countryId;

            this.customerLoyaltyService.GetDepartment(modelIn).then((ret) => {

                    this.departments = ret;

                    if (this.customerLoyaltyService.clientInfo.clientDepartmentId != 0) {
                        this.queryForm.controls.clientDepartment.setValue(this.departments.filter(department => department.departmentId === this.customerLoyaltyService.clientInfo.clientDepartmentId)[0]);
                    }
               
            });
        });

        //Load Municipalyties
        this.queryForm.controls.clientDepartment.valueChanges.subscribe(() => {
            let department = this.queryForm.controls.clientDepartment.value;
            let modelIn = new GetMunicipalityModelIn();
            modelIn.DepartmentId = department.departmentId;

            this.customerLoyaltyService.GetMunicipality(modelIn).then((ret) => {
                this.municipalities = ret;

                if (this.customerLoyaltyService.clientInfo.clientCityId != 0) {
                    this.queryForm.controls.clientCity.setValue(this.municipalities.filter(municipality => municipality.municipalityId === this.customerLoyaltyService.clientInfo.clientCityId)[0]);
                }
            });
        });
    }

    ngOnInit() {


        if (this.appConfig.CONFIG === undefined) {
            this.appConfig.ActiveUrl="/actualizacion-datos";
            this.router.navigate(['/']);
        }
        else {
            this.PATH=this.appConfig.CONFIG.path;

          

        
            //update controls
            this.queryForm.controls.documentClientNumber.setValue(this.customerLoyaltyService.clientInfo.documentClientNumber);
            this.queryForm.controls.clientNames.setValue(this.customerLoyaltyService.clientInfo.clientNames.trim());
            this.queryForm.controls.clientLastNames.setValue(this.customerLoyaltyService.clientInfo.clientLastNames.trim());
            this.queryForm.controls.clientAddress.setValue(this.customerLoyaltyService.clientInfo.clientAddress.trim());
            this.queryForm.controls.clientTelephone1.setValue(this.customerLoyaltyService.clientInfo.clientTelephone1.trim());
            this.queryForm.controls.clientTelephone2.setValue(this.customerLoyaltyService.clientInfo.clientTelephone2.trim());
            this.queryForm.controls.clientMail.setValue(this.customerLoyaltyService.clientInfo.clientMail.trim());
            this.queryForm.controls.clientBirthDate.setValue(new Date(this.customerLoyaltyService.clientInfo.clientBirthDate));
            this.queryForm.controls.clientGender.setValue(this.clientGenders.filter(gender => gender.id === this.customerLoyaltyService.clientInfo.clientGender)[0]);
            this.getDocumentsType(); 
                    
          

        }


    }
    getDocumentsType()
    {
        this.customerLoyaltyService.GetDocumentTypeClient(new GetDocumentTypeClientModelIn()).then((ret)=>{

                this.documentTypesClient=ret;
                if (this.customerLoyaltyService.clientInfo.documentTypeClientId != 0 && ret!=null) {
                    
                    let documentType=this.documentTypesClient.filter(typeDoc => typeDoc.documentTypeClientId === this.customerLoyaltyService.clientInfo.documentTypeClientId);
                    this.queryForm.controls.documentTypeClient.setValue(documentType[0]);
                }

                this.getCountries();
            
        })
    }
    getCountries(){
        //listamos paises
        this.customerLoyaltyService.GetCountry(new GetClientInfoModelIn()).then((retCountry) => {

            this.countries = retCountry;
            
            if (this.customerLoyaltyService.clientInfo.clientCountryId != 0 && retCountry!=null) {
                this.queryForm.controls.clientCountry.setValue(retCountry.filter(country => country.countryId === this.customerLoyaltyService.clientInfo.clientCountryId)[0]);
            }

           
        });
    }

    public EnabledControls() {

        this.IsEditing = (!this.IsEditing);
        
        if (this.IsEditing) {
            
            this.queryForm.controls.documentClientNumber.enable();
            this.queryForm.controls.clientNames.enable();
            this.queryForm.controls.clientLastNames.enable();
            this.queryForm.controls.clientAddress.enable();
            this.queryForm.controls.clientTelephone1.enable();
            this.queryForm.controls.clientTelephone2.enable();
            this.queryForm.controls.clientMail.enable();
            this.queryForm.controls.clientBirthDate.enable();
        }
        else{
            this.queryForm.controls.documentClientNumber.disable();
            this.queryForm.controls.clientNames.disable();
            this.queryForm.controls.clientLastNames.disable();
            this.queryForm.controls.clientAddress.disable();
            this.queryForm.controls.clientTelephone1.disable();
            this.queryForm.controls.clientTelephone2.disable();
            this.queryForm.controls.clientMail.disable();
            this.queryForm.controls.clientBirthDate.disable();
        }



    }

    SaveChanges()
    {  
        if(this.queryForm.status=='VALID')
        {
            
            let modelIn= new SetClientInfoModelIn();
            modelIn.DocumentTypeClientId=this.queryForm.controls.documentTypeClient.value.id;
            modelIn.DocumentRequestorNumber=this.queryForm.controls.documentClientNumber.value;
            modelIn.ClientNames=this.queryForm.controls.clientNames.value;
            modelIn.ClientLastNames=this.queryForm.controls.clientLastNames.value;
            modelIn.ClientCountryId=this.queryForm.controls.clientCountry.value.countryId;
            modelIn.ClientDepartmentId=this.queryForm.controls.clientDepartment.value.departmentId;
            modelIn.ClientCityId=this.queryForm.controls.clientCity.value.municipalityId;
            modelIn.ClientAddress=this.queryForm.controls.clientAddress.value;
            modelIn.ClientTelephone1=this.queryForm.controls.clientTelephone1.value;
            modelIn.ClientTelephone2=this.queryForm.controls.clientTelephone2.value;
            modelIn.ClientMail=this.queryForm.controls.clientMail.value;
            modelIn.ClientBirthDate=this.queryForm.controls.clientBirthDate.value;
            modelIn.ClientGender=this.queryForm.controls.clientGender.value.id;
            
            this.customerLoyaltyService.SetClientInfo(modelIn).then((ret)=>{
                if (ret.statusCode = ResponseStateEnum.OK) {
                    this.IsUpdated=true;
                    this.EnabledControls();
                }
            }).catch((e)=>console.log(e));


         
        }
        else
        {
            console.log(this.queryForm.errors);
        }
    }

}


