import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import { ReactiveFormsModule } from '@angular/forms';
import { LOCALE_ID } from '@angular/core';



//Frameworks
import { SuiModule } from 'ng2-semantic-ui';

//Routes
import { RouterModule, Routes } from "@angular/router";
import { APPROUTER } from "./app.routing";
import { InitComponent } from "./init.component";

//Pipes
import { MoneyFormatPipe } from "./pipes/MoneyFormatPipe";
import { NumberFormatPipe } from "./pipes/NumberFormatPipe";
import { FilterPipe } from "./pipes/FilterPipe";
import { FirstToUpperPipe } from "./pipes/FirstToUpperPipe";
import { OrdinalNumberingPipe } from "./pipes/OrdinalNumberingPipe";

//Components
import { MenuComponent } from "./components/MenuComponent/Menu.component";
import { ResumePointsComponent } from "./components/ResumePointsComponent/resumePoints.component";
import { BusComponent } from "./components/BusComponent/bus.component";
import { SeatComponent } from "./components/seatComponent/seat.component";

//Pages
import { PageNotFoundComponent } from "./pages/PageNotFoundComponent/pageNotFound.component";
import { PageHomeComponent } from "./pages/PageHomeComponent/pageHome.component";
import { PageRedemptionComponent } from "./pages/PageRedemptionComponent/pageRedemption.component";
import { PageSummaryTripComponent } from "./pages/PageSummaryTripComponent/pageSummaryTrip.component";
import { PageLoadingComponent } from "./pages/PageLoadingComponent/pageLoading.component";
import { PageResultTripComponent } from "./pages/PageResultTripComponent/pageResultTrip.component";
import { PageUpdateUserDataComponent } from "./pages/PageUpdateUserDataComponent/pageUpdateUserData.component";
import { PagePointMovementComponent } from "./pages/PagePointMovementComponent/pagePointMovement.component";
import { PagePointsRequestComponent } from "./pages/PagePointsRequestComponent/pagePointsRequest.component";


//Services
import { AppConfigService } from "./services/AppConfigService";
import { AuthenticationService } from "./services/AuthenticationService";
import { AgencyService } from "./services/AgencyService";
import { CustomerLoyaltyService } from "./services/CustomerLoyaltyService";



@NgModule({
  declarations: [
    InitComponent,
    /* PIPES */
    MoneyFormatPipe,
    NumberFormatPipe,
    FilterPipe,
    FirstToUpperPipe,
    OrdinalNumberingPipe,
    /* PAGES */
    PageNotFoundComponent,
    PageHomeComponent,
    PageRedemptionComponent,
    PageSummaryTripComponent,
    PageLoadingComponent,
    PageResultTripComponent,
    PageUpdateUserDataComponent,
    PagePointMovementComponent,
    PagePointsRequestComponent,
    /* COMPONENTS */
    MenuComponent,
    ResumePointsComponent,
    BusComponent,
    SeatComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(APPROUTER, { useHash: true}),
    SuiModule,
    FormsModule,
    BrowserAnimationsModule,
    HttpModule,
    ReactiveFormsModule
  ],
  providers: [
    AppConfigService,
    AuthenticationService,
    AgencyService,
    CustomerLoyaltyService,
    {provide: LOCALE_ID, useValue: "es-CO"}
  ],
  bootstrap: [InitComponent]
})
export class AppModule { }



