import { Component, OnInit } from '@angular/core';
import { FormsModule,FormControl,FormGroup,FormBuilder,Validators } from '@angular/forms';
import { Router } from "@angular/router";

//MODELS
import { SummaryRedeemPointsModel, SetRedeemPointsModelIn } from "../../models/TripsModel";
import { ResponseStateEnum } from "../../enums/ResponseStateEnum";
import { GetClientCardModelIn } from "../../models/CustomerLoyaltyModel";

//SERVICES
import { AppConfigService } from "../../services/AppConfigService";
import { CustomerLoyaltyService } from "../../services/CustomerLoyaltyService";

@Component({
    selector: 'pageSummaryTrip-component',
    templateUrl: './pageSummaryTrip.component.html',
    styleUrls: ['./pageSummaryTrip.component.scss']

})

export class PageSummaryTripComponent implements OnInit {

    tripActive: SummaryRedeemPointsModel;
    hasMail:boolean;
    acceptedConditions: boolean;
    cardNumber: number;
    message:string;
    form: FormGroup;
    IsMailSender:boolean;

    constructor(
        public customerLoyaltyService: CustomerLoyaltyService,
        private router: Router,
        private appConfig: AppConfigService,
        private fb: FormBuilder,
    ) {
        this.form= this.fb.group({
            cardNumber: new FormControl('',[Validators.required,Validators.pattern('[0-9]*'),Validators.maxLength(18)] ),
            acceptedConditions:new FormControl(false,[Validators.pattern('true')] ),
          });
    }

    ngOnInit() {
        this.hasMail=false;
      
        if (this.appConfig.CONFIG === undefined || this.customerLoyaltyService.tripActive===undefined) {
            this.router.navigate(['']);
        }
        else {
            this.tripActive = this.customerLoyaltyService.tripActive;
            
            this.hasMail=(this.customerLoyaltyService.clientInfo.clientMail!==undefined && this.customerLoyaltyService.clientInfo.clientMail!=null && this.customerLoyaltyService.clientInfo.clientMail!='');
            this.tripActive.clientMail=(this.hasMail?this.customerLoyaltyService.clientInfo.clientMail:'(Requerido)');
        }


    }


    FinishRedeem() {

        let modelIn = new SetRedeemPointsModelIn();

        modelIn.agencyDestinationId = this.tripActive.agencyDestinationId;
        modelIn.agencySaleId = this.tripActive.agencyOriginId;
        modelIn.cardNumber = Number(this.form.get('cardNumber').value);
        modelIn.departureOrReturn = "I";
        modelIn.documentsList = `'${this.appConfig.CONFIG.userIdentity}'`;
        modelIn.passengersCount = 1;
        modelIn.purchaseId = 0;
        modelIn.seatsList = `'${this.tripActive.seatId}'`;
        modelIn.tripDateDeparture = this.tripActive.tripDate;
        modelIn.tripKey = this.tripActive.tripKey;
        modelIn.valueToRedeem = this.tripActive.valueToRedeem;
        modelIn.clientMail=this.customerLoyaltyService.clientInfo.clientMail;




        this.customerLoyaltyService.SetRedeemPoints(modelIn).then((ret) => {
            if (ret.statusCode == ResponseStateEnum.OK) {

                this.customerLoyaltyService.tripActive.purchaseId = ret.data.purchaseId;

                //obtenemos los puntos actuales
                this.customerLoyaltyService.GetRedeemPoints().then((ret) => {

                    if (ret.statusCode = ResponseStateEnum.OK) {

                        if (ret.data.userIsTraveler == 'S') {

                            this.customerLoyaltyService.SetGetRedeemPointsOut(ret.data);

                            this.router.navigate(['/resultado-viaje']);
                        }
                    }
                })

               
            }
            else{
                this.message=ret.message;
            }

        }).catch((e) => console.log(e));
    }

    SendMailCardEvent()
    {
        this.IsMailSender=false;
        var modelIn = new GetClientCardModelIn();

        modelIn.documentClientNumber=this.customerLoyaltyService.clientInfo.documentClientNumber;
        modelIn.documentTypeClientId=this.customerLoyaltyService.clientInfo.documentTypeClientId;

        this.customerLoyaltyService.SendMailCustomerCard(modelIn).then((ret)=>{
            if (ret.statusCode == ResponseStateEnum.OK) {
                this.IsMailSender=true;
            } 
        }).catch(e=>console.log(e));
    }
}


